.Loader {
  display: block;
  position: relative;
}

.Ring {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 100px;
  height: 100px;
  left: calc(50% - 50px);
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  animation: spinning-rings 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border: 25px solid;
  border-color: orangered transparent transparent transparent;
}

.Ring:nth-child(1) {
  animation-delay: -0.45s;
}

.Ring:nth-child(2) {
  animation-delay: -0.3s;
}

.Ring:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes spinning-rings {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
